  line-height: 1.5;
}

p.large {
  color: #fff;
  font-size: 24px;
}

p.small {
  color: #fff;
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #fff;
  text-decoration: none;
}

a:hover {
